html, body {
  background-color: #eee;
}

.header,
.content {
  padding-left: 50px;
  padding-right: 50px;
}

.header {
  text-align: center;
  font-size: 35px;
  border-bottom: 1px solid;
  padding-top: 20px;
  padding-bottom: 20px;
}

.content {
  padding: 50px 0;
}

.checkbox + .checkbox {
  margin-top: 8px;
}

.group {
  margin-left: 20px;
}

.checkbox + .group,
.group + .group,
.group + .checkbox {
  margin-top: 8px;
}

.group_data {
  margin-top: 8px;
  margin-left: 30px;
}